import styles from "./Main.module.css";
import {Link} from "react-router-dom";
import React, {useEffect, useState} from 'react';
import axios from 'axios';

export default function Main () {
//
    const [hello, setHello] = useState("");

    useEffect(() => {
        event();
    }, []);

    const event = async () => {

        let res = null;

        try {
            res = await axios.get("/api/hello" );
        } catch (err) {
            res = null;
        }

        if (res.data !== undefined) {
            setHello(res.data);
        }

    }
//
//
    return (
        <>
            {hello}
        </>
//         // <div className={styles.container}>
//         //     <div className={styles.xTemperature}>22 Temperture</div>
//         //     <div className={styles.xYear}>
//         //         <div className={styles.xYearContent}>2022</div>
//         //         <div className={styles.total}>102,421,123</div>
//         //     </div>
//         // </div>
    )
}